import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './guards/auth.guard';


const routes: Routes = [
  {
    path: '', redirectTo: '/login',
    pathMatch: 'full'
  },
  {
    path: 'home', redirectTo: '/login',
    pathMatch: 'full',
  },
  {
    path: 'home',
    loadChildren: './home/home.module#HomePageModule',
  },
  {
    path: 'home/chat',
    loadChildren: './pages/chat/chat.module#ChatPageModule',
    // canActivate: [AuthGuard]
  },
  {
    path: 'stato',
    loadChildren: './pages/stato/stato.module#StatoPageModule',
    // canActivate: [AuthGuard]
  },
  {
    path: 'utenti',
    loadChildren: './pages/utenti/utenti.module#UtentiPageModule',
    // canActivate: [AuthGuard]
  },
  {
    path: 'login',
    loadChildren: './pages/login/login.module#LoginPageModule'
  },
  {
    path: 'user-chat',
    loadChildren: './pages/user-chat/user-chat.module#UserChatPageModule',
    // canActivate: [AuthGuard]
  },
  {
    path: 'add-friend-modal',
    loadChildren: './pages/add-friend-modal/add-friend-modal.module#AddFriendModalPageModule'
  },
  {
    path: 'registration',
    loadChildren: './pages/registration/registration.module#RegistrationPageModule'
  },
  { path: 'options-popover', loadChildren: './pages/options-popover/options-popover.module#OptionsPopoverPageModule' },
  { path: 'photo-modal', loadChildren: './pages/photo-modal/photo-modal.module#PhotoModalPageModule' },
  { path: 'landing', loadChildren: './pages/landing/landing.module#LandingPageModule' }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
